import { gql } from '@apollo/client';

export const GET_BUYER_OFFER = gql`
  query getOffer($offerId: ID!) {
    getOffer(OfferId: $offerId) {
      CounterOffer {
        BuyerCounter {
          OfferPrice
          SaleOfHomeContingency
          AppraisalContingency
          AppraisalContingencyDays
          FinanceContingency
          FinanceContingencyDays
          InspectionContingency
          InspectionContingencyDays
          ClosingPeriod
          Furniture
          FurnitureOther
        }
        SellerCounter {
          AppraisalContingency
          AppraisalContingencyDays
          FinanceContingency
          FinanceContingencyDays
          InspectionContingency
          InspectionContingencyDays
          OfferPrice
          SaleOfHomeContingency
          ClosingPeriod
          Furniture
          FurnitureOther
        }
      }
      CounterOfferHistory {
        BuyerCounter {
          AppraisalContingency
          SubmittedAt
          Status
          SaleOfHomeContingency
          RentBackInititated
          RentBackDays
          RentBackDailyAmount
          RentBack
          Position
          OfferPrice
          InspectionContingencyDays
          InspectionContingency
          FurnitureOther
          Furniture
          FinanceContingencyDays
          FinanceContingency
          ClosingPeriod
          AppraisalContingencyDays
        }
        SellerCounter {
          AppraisalContingency
          AppraisalContingencyDays
          ClosingPeriod
          FinanceContingency
          FinanceContingencyDays
          Furniture
          FurnitureOther
          InspectionContingency
          InspectionContingencyDays
          OfferPrice
          Position
          RentBack
          RentBackDailyAmount
          RentBackDays
          RentBackInititated
          SaleOfHomeContingency
          Status
          SubmittedAt
        }
      }
      Entity {
        EntityType
        IndividualOnBehalf
        Name
        TitleName
      }
      Funding {
        DownPayment
        LoanType
        PurchaseType
        CashProofOfFundsAmount
        FinancingProofOfFundsAmount
      }
      Offer {
        ClosingPeriod
        FinanceContingency
        Furniture
        FurnitureOther
        InspectionContingency
        OfferPrice
        SaleOfHomeContingency
        FinanceContingencyDays
        InspectionContingencyDays
        AppraisalContingencyDays
      }
      OfferId
      OfferNo
      Step {
        allowed_actions {
          action
          form_action
          next_step_id
        }
        id
        messages {
          buyer
          seller
        }
        name
      }
      StepId
      Property {
        SellerInfo {
          Email
          FirstName
          LastName
          Phone
        }
      }
    }
  }
`;

