// @flow
import React, { Fragment } from 'react';
import type { Element, AbstractComponent } from 'react';
import { Grid, makeStyles } from '@material-ui/core';
import { PageWrapper } from 'components/PageWrapper';
import _get from 'lodash.get';
import Header from './Header';
import Instruction from './Instruction';
import Details from './Details';
import History from './History';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import clsx from 'clsx';
import _isEmpty from 'lodash/isEmpty';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { OFFER_STEPS } from 'shared/Consts';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
}));

type Props = {
  offer: Object,
  isSeller: Boolean,
  loading: Boolean,
  refetch: Function,
};

const checkProps = ({ offer }) => !_isEmpty(offer);

const OfferDetails = ({
  offer,
  isSeller,
  loading,
  refetch,
}: Props): Element<typeof Fragment> => {
  const classes = useStyles();
  const offerStepId = _get(offer, 'StepId');
  const hasInstruction = [
    OFFER_STEPS.sellerNeedToReviewSuggestion,
    OFFER_STEPS.sellerNeedToCheckCounter,
    OFFER_STEPS.sellerNeedToReReviewSuggestion,
  ].includes(offerStepId);

  return (
    <>
      <Header offer={offer} isSeller={isSeller} refetch={refetch} />
      <PageWrapper>
        <BlockUi
          tag="div"
          blocking={loading}
          loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
          className={clsx(loading && classes.container)}
        >
          <Grid container justifyContent="center" spacing={2}>
            <Grid item xs={12} lg={8}>
              <History offer={offer} />
            </Grid>
            <Grid item xs={12} lg={4}>
              <Grid
                container
                direction="column"
                justifyContent="space-between"
                alignItems="flex-start"
                spacing={2}
              >
                {hasInstruction && (
                  <Grid item xs={12}>
                    <Instruction offerStepId={offerStepId} />
                  </Grid>
                )}
                <Grid item xs={12}>
                  <Details offer={offer} isSeller={isSeller} />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </BlockUi>
      </PageWrapper>
    </>
  );
};

export default (hideIfInvalidProps(checkProps)(
  OfferDetails,
): AbstractComponent<Props>);
