// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import {
  makeStyles,
  Container,
  Grid,
  AppBar,
  Typography,
} from '@material-ui/core';
import style from 'components/Header/headerStyle';
import type { Offer as TOffer } from 'types/Offer';
import _get from 'lodash.get';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import _isEmpty from 'lodash/isEmpty';
import SellerActions from './SellerActions';

type Props = {
  offer: TOffer,
  isSeller: Boolean,
  refetch: Function,
};

const useStyles = makeStyles(style);

const checkProps = ({ offer }) => !_isEmpty(offer);

const Header = ({ offer, isSeller, refetch }: Props): Element<'div'> => {
  const classes = useStyles();

  const offerNo = _get(offer, 'OfferNo');
  const offerStep = _get(
    offer,
    isSeller ? 'Step.messages.seller' : 'Step.messages.buyer',
  );

  return (
    <AppBar position="static" color="default" className={classes.header}>
      <Container className={classes.container}>
        <Grid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Grid item xs={12} lg={6}>
            <Typography variant="h3">
              {offerNo}: {offerStep}
            </Typography>
          </Grid>
          <Grid item xs={12} lg={6}>
            {isSeller && <SellerActions offer={offer} refetch={refetch} />}
          </Grid>
        </Grid>
      </Container>
    </AppBar>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(Header),
): AbstractComponent<Props>);
